.container {
    min-height: 100vh;
    width: 85%;
    border: 1px solid;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}

@media screen and (max-width: 768px) {
    .container {
        justify-content: center;
    }
}